@keyframes lum-fade {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes lum-fadeZoom {
  0% {
    opacity: 0;
    transform: scale(.5);
  }

  100% {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes lum-loader-rotate {
  0% {
    transform: translate(-50%, -50%)rotate(0);
  }

  50% {
    transform: translate(-50%, -50%)rotate(-180deg);
  }

  100% {
    transform: translate(-50%, -50%)rotate(-360deg);
  }
}

@keyframes lum-loader-before {
  0% {
    transform: scale(1);
  }

  10% {
    transform: scale(1.2)translateX(6px);
  }

  25% {
    transform: scale(1.3)translateX(8px);
  }

  40% {
    transform: scale(1.2)translateX(6px);
  }

  50% {
    transform: scale(1);
  }

  60% {
    transform: scale(.8)translateX(6px);
  }

  75% {
    transform: scale(.7)translateX(8px);
  }

  90% {
    transform: scale(.8)translateX(6px);
  }

  100% {
    transform: scale(1);
  }
}

@keyframes lum-loader-after {
  0% {
    transform: scale(1);
  }

  10% {
    transform: scale(1.2)translateX(-6px);
  }

  25% {
    transform: scale(1.3)translateX(-8px);
  }

  40% {
    transform: scale(1.2)translateX(-6px);
  }

  50% {
    transform: scale(1);
  }

  60% {
    transform: scale(.8)translateX(-6px);
  }

  75% {
    transform: scale(.7)translateX(-8px);
  }

  90% {
    transform: scale(.8)translateX(-6px);
  }

  100% {
    transform: scale(1);
  }
}

.lum-lightbox {
  background: #0009;
}

.lum-lightbox-inner {
  inset: 2.5%;
}

.lum-lightbox-inner img {
  position: relative;
}

.lum-lightbox-inner .lum-lightbox-caption {
  color: #fff;
  max-width: 700px;
  text-align: center;
  margin: 0 auto;
}

.lum-loading .lum-lightbox-loader {
  width: 66px;
  height: 20px;
  animation: 1.8s linear infinite lum-loader-rotate;
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.lum-lightbox-loader:before, .lum-lightbox-loader:after {
  content: "";
  width: 20px;
  height: 20px;
  background: #ffffffe6;
  border-radius: 20px;
  margin-top: -10px;
  display: block;
  position: absolute;
  top: 50%;
}

.lum-lightbox-loader:before {
  animation: 1.8s linear infinite lum-loader-before;
  left: 0;
}

.lum-lightbox-loader:after {
  animation: 1.8s linear -900ms infinite lum-loader-after;
  right: 0;
}

.lum-lightbox.lum-opening {
  animation: .18s ease-out lum-fade;
}

.lum-lightbox.lum-opening .lum-lightbox-inner {
  animation: .18s ease-out lum-fadeZoom;
}

.lum-lightbox.lum-closing {
  animation: .3s ease-in reverse lum-fade;
}

.lum-lightbox.lum-closing .lum-lightbox-inner {
  animation: .3s ease-in reverse lum-fadeZoom;
}

.lum-img {
  transition: opacity .12s ease-out;
}

.lum-loading .lum-img {
  opacity: 0;
}

.lum-gallery-button {
  text-indent: 150%;
  white-space: nowrap;
  height: 100px;
  max-height: 100%;
  width: 60px;
  cursor: pointer;
  background: none;
  border: 0;
  outline: 0;
  margin: 0;
  padding: 0;
  position: absolute;
  top: 50%;
  overflow: hidden;
  transform: translateY(-50%);
}

.lum-close-button {
  width: 32px;
  height: 32px;
  opacity: .3;
  position: absolute;
  top: 5px;
  right: 5px;
}

.lum-close-button:hover {
  opacity: 1;
}

.lum-close-button:before, .lum-close-button:after {
  content: " ";
  height: 33px;
  width: 2px;
  background-color: #fff;
  position: absolute;
  left: 15px;
}

.lum-close-button:before {
  transform: rotate(45deg);
}

.lum-close-button:after {
  transform: rotate(-45deg);
}

.lum-previous-button {
  left: 12px;
}

.lum-next-button {
  right: 12px;
}

.lum-gallery-button:after {
  content: "";
  width: 36px;
  height: 36px;
  border-top: 4px solid #fffc;
  display: block;
  position: absolute;
  top: 50%;
}

.lum-previous-button:after {
  border-left: 4px solid #fffc;
  border-radius: 3px 0 0;
  left: 12%;
  transform: translateY(-50%)rotate(-45deg);
  box-shadow: -2px 0 #0003;
}

.lum-next-button:after {
  border-right: 4px solid #fffc;
  border-radius: 0 3px 0 0;
  right: 12%;
  transform: translateY(-50%)rotate(45deg);
  box-shadow: 2px 0 #0003;
}

@media (max-width: 460px) {
  .lum-lightbox-image-wrapper {
    -webkit-overflow-scrolling: touch;
    display: flex;
    overflow: auto;
  }

  .lum-lightbox-caption {
    width: 100%;
    position: absolute;
    bottom: 0;
  }

  .lum-lightbox-position-helper {
    margin: auto;
  }

  .lum-lightbox-inner img {
    max-width: none;
    max-height: none;
  }
}

